@import url("https://fonts.googleapis.com/css?family=Roboto");
@import url("https://fonts.googleapis.com/icon?family=Material+Icons");
* {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
  box-sizing: border-box; }

a {
  text-decoration: none;
  color: inherit; }

.admin-panel {
  display: flex;
  flex-wrap: nowrap; }

.app {
  height: 100vh;
  background-color: #FDFCFD;
  color: #5F5B6B; }

.homepage {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 16px; }
  .homepage__title {
    font-size: 100px;
    font-weight: bold; }

.test__hidden {
  display: none; }

.training__hidden {
  display: none; }

.button {
  padding: 8px 16px;
  border: 0;
  border-radius: 2px;
  background: #182825;
  color: white;
  font-size: 24px; }
  .button__float {
    width: 40px;
    height: 40px;
    position: fixed;
    left: 20px;
    top: 15px;
    border: none;
    border-radius: 50%;
    background-color: white;
    color: #182825; }
  .button__iconButton {
    border: 0;
    background: none;
    color: #182825; }
  .button__icon {
    font-family: 'Material Icons';
    font-weight: normal;
    font-style: normal;
    font-size: 24px;
    /* Preferred icon size */
    display: inline-block;
    line-height: 1;
    text-transform: none;
    letter-spacing: normal;
    word-wrap: normal;
    white-space: nowrap;
    direction: ltr;
    /* Support for all WebKit browsers. */
    -webkit-font-smoothing: antialiased;
    /* Support for Safari and Chrome. */
    text-rendering: optimizeLegibility;
    /* Support for Firefox. */
    -moz-osx-font-smoothing: grayscale;
    /* Support for IE. */
    font-feature-settings: 'liga'; }

.navigation {
  color: white;
  background: #182825; }
  .navigation__list {
    display: flex;
    justify-content: flex-end;
    margin: 0;
    list-style-type: none; }
    .navigation__list__item {
      margin: 20px;
      padding: 8px 16px;
      border: 0;
      border-radius: 2px;
      background: #7F7979; }

.score-counter {
  display: flex;
  justify-content: flex-end;
  margin: 16px 16px 0;
  font-size: 30px;
  padding: 16px;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2); }
  .score-counter__hidden {
    display: none; }

.rules {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin: 16px;
  padding: 16px;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2); }
  .rules__text {
    font-size: 25px; }
  .rules__start {
    margin-top: 16px;
    max-width: 200px; }
  .rules__hidden {
    display: none; }

.timer {
  display: flex;
  align-items: center;
  margin: 16px;
  height: 50px;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2); }
  .timer__bar {
    background-color: red;
    height: 10px; }
  .timer__hidden {
    display: none; }

.question-box {
  margin: 16px;
  padding: 16px;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2); }
  .question-box__hidden {
    display: none; }
  .question-box__text {
    font-size: 30px;
    margin-bottom: 16px; }
  .question-box__expired {
    display: flex;
    flex-direction: column;
    align-items: center; }
  .question-box__answers {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    align-items: center;
    min-height: 300px;
    margin-top: 16px;
    color: white; }
    .question-box__answers__answer {
      padding: 16px 16px;
      width: 40%;
      background-color: #C1BDB3; }
      .question-box__answers__answer:hover {
        background-color: grey; }
    .question-box__answers__correct {
      background-color: greenyellow; }
    .question-box__answers__wrong {
      background-color: red; }

.question-data-row__answer {
  display: block; }

.question-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 432px;
  height: calc(100vh - 75px);
  padding: 16px;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2); }
  .question-form__textarea {
    width: 100%;
    resize: none;
    padding: 10px;
    margin-bottom: 16px;
    font-size: 16px; }
  .question-form__button {
    margin-top: 16px; }
  .question-form__hidden {
    display: none; }

.universal-table {
  max-height: calc(100vh - 107px);
  width: 100%;
  margin: 16px 0 0 16px;
  overflow-y: scroll;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
  font-size: 13px;
  line-height: 2rem; }
  .universal-table__table {
    width: 100%;
    border-collapse: collapse; }
  .universal-table__header {
    font-size: 18px;
    padding: 16px 16px 16px 0;
    font-weight: inherit; }
  .universal-table td {
    padding: 10px;
    border-top: 1px solid rgba(0, 0, 0, 0.12); }



